export default (query) => {
    let queryStr = '?'

    if(query.title && query.title.trim().length !== 0) {
        queryStr += `title=${query.title.trim()}&`
    } else {
        // queryStr+='title=&'
    }

    if(query.location && query.location.trim().length !== 0) {
        queryStr += `location=${query.location.trim()}&`
    } else {
        // queryStr+='location=&'
    }

    if(query.remote && query.remote.trim().length !== 0) {
        queryStr += `remote=${query.remote.trim()}&`
    } else {
        // queryStr+='remote=&'
    }

    if(query.jobType && query.jobType.trim().length !== 0) {
        queryStr += `jobtype=${query.jobtype.trim()}&`
    } else {
        // queryStr+='jobtype=&'
    }

    if(query.salary && query.salary.trim().length !== 0) {
        queryStr += `salary=${query.salary.trim()}&`
    } else {
        // queryStr+='salary=&'
    }

    if(query.skills && query.skills.length !== 0) {
        queryStr += `skills=${query.skills.trim()}&`
    } else {
        // queryStr+='skills='
    }

    return queryStr 
}