<template>
    <div class="ct-container pb-6">
        <div v-if="suggest_search" class="mt-6">
            <div class="mb-4 search-title">Popular Search</div>
            <div class="suggest-search d-flex flex-wrap" style="max-width: 50rem">
                <span v-for="item in suggest_searchs" :key="item"
                    @click="searchJobBulk(item)"
                    class="suggest_item px-4 py-1 primary mr-3 mb-3">
                    {{ item.title }}
                </span>
            </div>
        </div>

        <div v-else>
        <div class="selected-info d-flex align-center ml-lg-3 ml-md-3 mt-1">
            <div class="info-text mr-5" v-if="getSelectedJob.title">{{ getSelectedJob.title }}</div>
            <div class="info-text mr-5" v-if="getSelectedJob.location">{{ getSelectedJob.location }}</div>
            <div class="info-text mr-5" v-if="getSelectedJob.job_type">{{ getSelectedJob.job_type }}</div>
            <div class="info-text mr-4" v-if="getSelectedJob.salary">{{ getSelectedJob.salary }}</div>

            <!-- <img style="height: 2rem; width: auto" 
                src="../assets/new_file01/save_job.png" alt="save_job"
            > -->
        </div>

        <div class="loading-container ml-lg-2 ml-md-2" >
            <v-text-field
                v-if="show_liveload"
                height="1"
                color="primary"
                :loading="live_loading"
                disabled
            ></v-text-field>
        </div>
 
        <transition-group tag="ul" name="anime">
            <li v-for="(job) in getResults[getCurTab.tabName]" :key="job.id" class="ml-lg-2 ml-md-2">
                <JobCard :job="job" @resLoading="($event) => live_loading = $event" />
            </li>
        </transition-group>
        </div>
    </div>
</template>

<script>
    import { mapGetters, mapActions } from 'vuex'
    import queryBuilder from '../services/queryBuilder'

    import JobCard from '../components/JobCard'


export default {
    name: 'Live_results',
    data: () => ({
        live_loading: false,
        suggest_searchs: [
            { title: "React Developer", location: '' },
            { title: "UX Designer", location: '' },
            { title: "Graphic Designer", location: '' },
            { title: "UI Designer", location: '' },
            { title: "Frontend Developer", location: '' },
            { title: "Manager", location: '' },
            { title: "Backend Developer", location: '' },
            { title: "Content Creator", location: '' },
            { title: "IT Manager", location: '' }
        ]
    }),
    components: {
        JobCard,
        // DragHandle,
        // DragItem,
        // DragList
    },
    methods: {
        ...mapActions(['searchJobBulk','getSingleJob','resetLive']),
        realTimefetch() {
            setInterval(async () => {

                // return
                
                let value = Math.floor(Math.random() * 10)

                if (value > 10) {

                    if(this.live_loading || !this.$route.query.title || this.getLoading) {
                        return
                    }

                    this.live_loading = true

                    let singleJob = await this.getSingleJob(queryBuilder(this.$route.query))
                    
                    let duplicate = this.getResults.live_result.find(item => {
                        return item.link == singleJob.link
                    })

                    if(!duplicate || !this.live_loading) {
                    this.getResults.live_result.unshift(singleJob)
                    this.live_loading = false
                    }

                    this.live_loading = false
                
                }
                
            }, 10000)
        }
        
    },
    computed: {
        ...mapGetters(['getCurTab','getResults','getLoading','getSelectedJob']),
        show_liveload() {
            return this.getCurTab.index === 0 
                && this.live_loading 
                && this.getResults.live_result.length !== 0
        },
        suggest_search() {
            return this.getResults.live_result.length === 0 && !this.$route.query.title && this.getCurTab.index === 0
        },

    },
    mounted() {

        if(Object.keys(this.$route.query).length == 0) {
            this.resetLive()
        }

        if(this.getResults['live_result'].length === 0 && this.$route.query.title) {
            this.$store.commit('setSelectedJob', this.$route.query)
            this.searchJobBulk(this.$route.query)
        }

        this.realTimefetch()
    }
}
</script>

<style scoped>
    .ct-container {
        padding-left: 11.28rem;
    }

    /* header style */
    .info-text {
        color: #838CA3;
        font-size: .8rem;
    }

    .draggable {
        max-width: 32.2rem;
        margin-bottom: 2rem;
    }

    ul {
       list-style-type: none; /* Remove bullets */
        padding: 0; /* Remove padding */
        margin: 0;
    }

    .loading-container {
        height: 2rem;
        max-width: 29.8rem;
        margin-bottom: 6px;
        /* padding: 0 4px; */
        padding-left: 5px;
    }

    .search-title {
        color: rgb(165, 165, 165);
        font-size: 15px;
        font-weight: 500;
    }

    /* suggest items */
    .suggest_item {
        color: white;
        font-weight: 500;
        font-size: 12px;
        border-radius: 3rem;
        cursor: pointer;
    }


    /* small screen config */
    @media only screen and (max-width: 998px) {
        .ct-container {
            padding-left: 0rem;
            max-width: 30rem;
            margin: 0 auto;
        } 

        .ct-panels {
            max-width: 5rem;
        }
    }

    @media only screen and (max-width: 515px) {
        .ct-container {
            padding: 0 12px
        } 
    }

    /* animation style */
    .anime-enter-active {
        animation: slide-in 0.3s ease-in forwards;
    }

    .anime-leave-active {
        transition: slide-out 0.3s ease-in forwards;
    }

    @keyframes slide-in {
        from {
            transform: translateY(50px);
            opacity: 0;
        }
        to {
            transform: translateY(0);
            opacity: 1;
        }
    }
    
</style>