<template>
    <div class="outerbox">
    <div :id="`parent${job.id}`" style="position: relative;">
        <div 
            class="ct-overlay ct-display-off font-weight-bold d-none justify-center"
            :class="[
                swipe === -1 ? 'd-flex turnLeft' : '',
                swipe === 1 ? 'd-flex turnRight' : '',
                swipe === 'down' ? 'd-flex' : ''
            ]">
            <span class="info-tag" :class="[
                swipe === -1 ? 'turnRight' : '',
                swipe === 1 ? 'turnLeft' : ''
            ]">{{ currentOperation }}</span>
        </div>
        <div class="card-container white mb-5" 
            :class="[
                desc_expand ? 'extra_bordor-rad' : '',
                swipe === -1 ? 'turnLeft' : '',
                swipe === 1 ? 'turnRight' : ''
            ]" 
        >

        <!-- level one -->
        <div class="header d-flex align-center justify-space-between no-select">

            <v-tabs height="40" hide-slider v-model="current_tab" class="left-border" >
                <v-tab class="header-tab" @change="resetResult(0)">   
                    <img style="height: 1.2rem; width: auto" v-if="current_tab === 0"
                        src="../assets/new_file01/briefcase_active.png" alt="general">

                    <img style="height: 1.2rem; width: auto" v-else
                        src="../assets/new_file01/briefcase_inactive.png" alt="general">
                </v-tab>

                <div class="tab-divider" style="width: 1px"></div>

                <v-tab @change="resetResult(1)"
                    class="header-tab d-flex align-center justify-center" >   
                    <img style="height: 1.2rem; width: auto" v-if="current_tab === 1"
                        src="../assets/new_file01/note_active.png" alt="general">

                    <img style="height: 1.2rem; width: auto" v-else
                        src="../assets/new_file01/note_inactive.png" alt="general">
                </v-tab>

                <div class="tab-divider" style="width: 1px"></div>

                <v-tab @change="resetResult(2)"
                    class="header-tab d-flex align-center justify-center" >   
                    <img style="height: 1.2rem; width: auto" v-if="current_tab === 2"
                        src="../assets/new_file01/contact_active.png" alt="general">

                    <img style="height: 1.2rem; width: auto" v-else
                        src="../assets/new_file01/contact_inactive.png" alt="general">
                </v-tab>

                <div class="tab-divider" style="width: 1px" v-if="job.skill.score > 0"></div>

                <v-tab @change="resetResult(3)" v-if="job.skill.score > 0"
                    class="header-tab d-flex align-center justify-center" >  

                    <!-- <div class="percent-text red" v-if="job.current_tab === 3">
                        {{job.skill.score}}
                        <span style="font-size: 10px">%</span>
                    </div> -->

                    <div class="percent-text d-flex align-end"
                        :class="[ current_tab === 3 ? 'percent-active': '']"
                    >{{ job.skill.score }}
                        <div style="font-size: 10px">%</div>
                    </div>
                </v-tab>
            </v-tabs>
    
            <div class="pr-4 pt-1" v-if="getCurTab.index < 3">
                <v-tooltip right content-class="tooltip">
                    <template v-slot:activator="{ on }">
                        <img v-on="on" @click="insertToSection({ card: job })"
                            style="height: 0.86rem; width: auto" 
                            src="../assets/new_file01/movei.png" alt="interseted">
                    </template>
                    <span>{{ insertName[getCurTab.index] }}</span>
                </v-tooltip>
            </div>
        </div>

        <!-- level two -->
        <div class="ct-body mt-3 no-select" :class="[!desc_expand ? 'pb-2' : 'pb-4']">
            <!-- main tab content start -->
            <v-tabs-items v-model="current_tab" class="remove-space" >
                <v-tab-item>
                    <div class="ct-pad-horizon">
                        <div class="ct-handler">
                        <div class="ct-body__head d-flex justify-space-between">
                            <div style="max-width: 80%">
                                <div class="job--heading primary--text">{{ job.title }}</div>
                                <div class="job--company">{{ job.company }}</div>
                            </div>
                            <div class="job--time">{{ job.time }}</div>
                        </div>

                        <div class="d-flex justify-space-between my-3" >
                            <div class="job--salary">{{ job.salary }}</div>

                            
                            <div style="font-size: .82rem; padding: 1px 0"
                                class="apply-btn text-right px-3 rounded" 
                                v-if="desc_expand">
                                <a :href="job.apply" target="_blank" class="apply-btn">
                                    <span style="transform: translateY(9px)">Apply</span>
                                </a>
                            </div>
                            
                        </div>
                        </div>

                        <div class="ct-body__end " >
                            <div style="position: relative;">
                                <div class="ct-handler">
                                <div :class="[desc_expand ? '' : 'pb-2']" style="padding-bottom: 18px"
                                    class="job-description" v-if="!desc_expand">
                                    <div v-if="job.description.length < 125 ">{{ job.description }}</div>
                                    <div v-else>{{ job.description.split("").slice(0, 118).join("") }}...</div>
                                </div>
                                <v-expand-transition >
                                    <div v-html="job.more_description"
                                        class="job-description-expand pr-2" v-if="desc_expand">
                                    </div>
                                </v-expand-transition>
                                </div>
                                <v-fade-transition>
                                    <span class="read--more primary--text pb-2" :class="[job.description.length === 0 ? 'pb-3': '']"
                                        v-show="!desc_expand"
                                        @click="desc_expand=true"
                                    >
                                        Read More
                                    </span>
                                </v-fade-transition>
                            </div>
                        </div>
                    </div>
                </v-tab-item>

                <v-tab-item>
                    <div class='ct-pad-horizon'>
                        <textarea placeholder="Notes..."
                            @input="debouncedOnChange(job)"
                            v-model="job.notes" class="user_notes" rows="5">
                        </textarea>
                    </div>
                </v-tab-item>

                <v-tab-item class="pb-2">
                    <div style="font-size: 0.88rem" class="ct-pad-horizon">
                        <div style="color: #838CA3" class="mb-1">Recruiter Name: {{ job.recruiter.name }}</div>
                        <div style="color: #838CA3" class="mb-1">Recruiter Phone: {{ job.recruiter.phone }}</div>
                        <div style="color: #838CA3">Recruiter Email: {{ job.recruiter.email }}</div>
                    </div>
                </v-tab-item>

                <v-tab-item class="pb-2" v-if="job.skill.score > 0">
                    <div style="font-size: 0.88rem" class="ct-pad-horizon d-flex flex-wrap">
                        <div class="px-4 primary white--text rounded mr-3 mb-3"
                            v-for="skillName in job.skill.skills" :key="skillName">
                            {{ skillName }}
                        </div>
                    </div>
                </v-tab-item>
            </v-tabs-items>
        </div>
    </div>
    </div>
    </div>
</template>

<script>
    import { mapGetters, mapActions } from 'vuex'
    import axios from 'axios'
    import moment from 'moment'
    import _debounce from 'lodash.debounce'
    import * as Draggabilly from 'draggabilly/draggabilly'

    export default {
        props: ['job'],
        data: () => ({
            current_tab: 0,
            job_notes: '',
            desc_expand: false,
            insertName: [
                'Move to Live Result',
                'Move to Interested',
                'Move to Applied',
                'Move to Invited offer'
            ],
            swipe: 0,
            message: ''
        }),
        computed: {
            ...mapGetters(['getCurTab']),
            debouncedOnChange () {
                return _debounce(this.onNoteChange, 3000);
            },
            currentOperation() {
                let message = ''
                switch(this.swipe) {
                    case -1:
                        message = this.insertName[this.getCurTab.index - 1]
                        break
                    case 1:
                        message = this.insertName[this.getCurTab.index + 1]
                        break
                    case 'down':
                        message = 'Remove'
                        break
                }

                return message
            },
            shortDescrption() {
                if(this.job.description.length > 135) {
                    return this.job.description.split("").slice(0, 135).join("")+'...'
                } else {
                    return this.job.description
                }
            },
            postedTime() {
                if(typeof this.job.posted === 'number') {
                    return moment(this.job.posted).fromNow()
                } else {
                    return this.job.posted
                }
            },
            
        },
        methods: {
            ...mapActions(['insertSection','removeSection']),
            resetResult(tabIndex) {
                if(this.current_tab !== tabIndex) {
                    this.current_tab = tabIndex
                }
                this.desc_expand = false
            },
            insertToSection({ card }) {
                this.insertSection({card, operationKey: 1 })
            },
            async onNoteChange() {
                try {
                    await axios.post('/api/jobs/add-notes', {
                        currentIndex: this.getCurTab.index,
                        note: this.job.notes,
                        subID: this.job._id
                    })
                } catch (err) {
                    console.log("Err: ",err)
                }
            },
        },
        mounted() {
            let touchDevice = false
            var elem = document.querySelector(`#parent${this.job.id}`);
            let params = {
                handle: '.ct-handler',
                axis: 'x'
            }

            !touchDevice ? delete params.axis : ''

            let vertScroll = false;
            let initialY = 0;
            // let initAdjustment = 0

            var draggie = new Draggabilly(elem, params)

            let vm = this

            draggie.on( 'dragStart', function( event ) {
                initialY = event.pageY
            })

            draggie.on( 'dragMove', function(event) {
                // vm.$emit('resLoading', false)
                elem.style.cursor = 'grabbing';
                elem.style.zIndex = '1000';

                let moveSpaceX = touchDevice ? 30 : 90
                let moveSpaceY = touchDevice ? -20 : -50
                
                let { x, y } = draggie.position

                //vertical---
                if (vertScroll == false && (event.pageY - initialY) > 30) {
                    // draggie.disable()
                    // vertScroll = true;
                    // initAdjustment = event.pageY-initialY
                }

                // window.scrollBy(0,-(event.pageY - initialY) + initAdjustment)

                if(x > moveSpaceX && vm.getCurTab.index < 3) {
                    vm.swipe = 1
                    return
                }

                if(x < moveSpaceY && vm.getCurTab.index > 0) {
                    vm.swipe = -1
                    return
                }

                if(y > 50) {
                    vm.swipe = 'down'
                    return
                }

                vm.swipe = 0
            })

            draggie.on( 'dragEnd', function() {

                vertScroll = false;
                initialY = 0;
                // initAdjustment = 0
                draggie.enable()
              
                let { x, y } = draggie.position

                if(x > 90 && vm.getCurTab.index < 3) {
                    vm.insertSection({ card: vm.job, operationKey: 1 })
                }

                if(x < -50 && vm.getCurTab.index > 0) {
                    vm.insertSection({ card: vm.job, operationKey: -1 })
                }

                if(y > 50) {
                    vm.removeSection(vm.job)
                }

                draggie.setPosition( 0, 0 )
                elem.style.zIndex = '0';
                elem.style.cursor = 'grab';
                // vm.$emit('resLoading', true)
            })
        }
    }
</script>

<style scoped>
    .card-container {
        max-width: 30rem;
        box-shadow: 0px 3px 30px rgba(0, 70, 254, 0.07);
        transition: 0.15s;
        border-radius: 6px;
        overflow: hidden;
        outline: none;
    }

    .turnLeft {
        transform: rotate(-10deg);
    }

    .turnRight {
        transform: rotate(10deg);
    }

    /* drag style start */
    .outerbox {
        max-width: 30rem;
        background-color: #F9FAFF;
        background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='6' ry='6' stroke='%23838CA3FF' stroke-width='2' stroke-dasharray='4%2c 9' stroke-dashoffset='12' stroke-linecap='square'/%3e%3c/svg%3e");
        border-radius: 6px;     
    }

    .no-select {
        -webkit-touch-callout: none; /* iOS Safari */
        -webkit-user-select: none; /* Safari */
        -khtml-user-select: none; /* Konqueror HTML */
        -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
        user-select: none; 
    }

    .card-container:hover {
        box-shadow: 0px 20px 100px rgba(0, 43, 156, 0.15);
        cursor: pointer;
    }

    .ct-handler:hover {
        cursor: grab;
    }

    .ct-handler:active {
        cursor: grabbing;
    }

    .info-tag {
        padding: 5px 25px;
        border: solid 1px #0046FE;
        border-radius: 4px;
        font-weight: 700;
        margin-top: 1rem;
        font-size: 15px;
        color: #0046FE;
    }

    .ct-overlay {
        position: absolute; 
        z-index: 1; 
        height: 100%; 
        width: 100%; 
        border-radius: 6px; 
        pointer-events: none; 
        background-color: #f9faff9c;
        justify-content: center;
        align-items: center;
        transition: 0.2s;
    }

    .header-tab {
        background-color: #F7F9FF;
        cursor: pointer;
        min-width: 58px;
    }

    .left-border {
        border-top-left-radius: 6px !important;
    }

    .tab-divider {
        width: 1px;
        height: 35px;
        background-color: #E8EEFF;
    }

    .percent-text {
        font-weight: 700;
        font-size: 15px;
        padding: 0 !important;
        color: #CAD9FF
    }

    .percent-active {
        color: #7BA0FF
    }

    /* card body */
    /* .ct-body {
        padding: 12px 18px 0.5px 18px;
    } */

    .ct-pad-horizon {
        padding: 0px 18px !important;
    }

    .apply-btn {
        background-color: rgb(0,70,254);
        color:white;
        transition: 0.2s;
        text-decoration: none;
    }
    .apply-btn:hover {
        background-color: #1656f7;
    }

    .job--heading {
        font-weight: 700;
        font-size: 0.95rem;
    }

    .job--company {
        color: #838CA3;
        font-size: 0.87rem;
    }

    .job--salary {
        color: #002178;
        font-size: 0.86rem;
        font-weight: 500;
    }   

    .job--time {
        color: #959db1;
        font-size: .7rem;
    }

     .job-description {
        color: #8e98b1;
        font-size: 0.8rem;
        padding-right: 3px;
        margin: 0;
        /* transform: translateY(14px); */
    }

    .read--more {
        font-size: .84rem;
        font-weight: 600;
        cursor: pointer;
        position: absolute; 
        bottom: 0;
        right: 0;
    }

    .job-description-expand {
        color: #8e98b1;
        font-size: 0.8rem;
        font-weight: 200;
        max-height: 12rem;
        overflow-y: scroll;
        /* transform: translateY(8px); */
    }

    /* scroll bar style start */

    .job-description-expand::-webkit-scrollbar {
        width: 4px;
    }

    /* Track */
    .job-description-expand::-webkit-scrollbar-track {
        background: #f1f1f1; 
    }
    
    /* Handle */
    .job-description-expand::-webkit-scrollbar-thumb {
        background: #0046FE; 
    }

    /* Handle on hover */
    .job-description-expand::-webkit-scrollbar-thumb:hover {
        background: #0046FE; 
    }

    /* scroll bar style start end */

    /* .ct-pad-horizon {
        padding: 0px 18px !important;
    } */


    /* user note pad style */
    .user_notes {
        
        flex: 1;
        outline: none;
        font-size: 0.84rem;
        color: rgb(124, 124, 124);
        resize: none;
    }

    .user_notes::placeholder {
        color: rgb(209, 209, 209)
    }

    /* tool tip style */
    .tooltip {
        color: #838CA3;
        font-size: 0.8rem;
        background-color: #F1F5FF;
    }

    /* notes scroll start*/

        .user_notes::-webkit-scrollbar {
            width: 4px;
        }

        /* Track */
        .user_notes::-webkit-scrollbar-track {
            background: #f1f1f1; 
        }
        
        /* Handle */
        .user_notes::-webkit-scrollbar-thumb {
            background: #0046FE; 
        }

        /* Handle on hover */
        .user_notes::-webkit-scrollbar-thumb:hover {
            background: #0046FE; 
        }
    /* notes scroll end*/


    /* custom styles */
    .ct-height {
        height: 334px;
    }

    .original-height {
        height: initial
    }
    
</style>